import React, { Component } from 'react';
import './App.css';
import Movie from './Movie';


class App extends Component{
  //Render : componentWillMount() -> render() -> componentDidMount()
  //Update : componentWillReceiveProps() -> shouldComponentUpdate() -> componentWillUpdate() -> rednder() -> componentDidUpdate()

  state={

  }

  componentDidMount(){
    this._getMovies();
  }

  _renderMovies = () =>{
    const movies = this.state.movies.map(movie =>{
      return <Movie title={movie.title}
                    poster={movie.medium_cover_image}
                    key={movie.id}
                    genres={movie.genres}
                    synopsis={movie.synopsis}
      />
    })
    return movies;
  }

  //async, await
   _getMovies = async () => { //async는 이전 작업이 끝나야 그다음 작업이 시작하는 형태가 아니다.
    const movies = await this._callApi() //this._callApi()작업이 끝나기를 기다리고, return 값이 무엇이던간에 변수에 저장해라. 고로 다음 문장은 컴포넌트가 완료될 때까지 실행되지 않음
    this.setState({
      movies // == movies : movies
    })  
  }

  _callApi = () => {
    //promises
    //fetch('https://google.com') fetch로 Url을 json으로 불러 올 수 있음.
    return fetch('https://yts.mx/api/v2/list_movies.json?sort_by=download_count') 
    .then(response => response.json()) //우리가 볼 수 있게 json을 가공하자.
    .then(json => json.data.movies) //가공된 json을 json.data.movies를 return한다.
    .catch(err => console.log(err)) //에러가 있으면 에러내용 출력
                  // .catch(function(err){
                  //   console.log(err)
                  // })
  }

  render(){
    const { movies } = this.state;
    return(      
      <div className={movies ? "App" : "App--loading"}>
        {movies ? this._renderMovies() : "Loding"}
      </div>
    )
  }
}

export default App;
